import React from 'react'
import { Link } from 'gatsby';

import Container from 'components/Container'
import Button from 'components/Button'
import Box from 'components/Box';
import Text from 'components/Text';

const pages = [
  [{ name: 'Marketing', href: '/marketing' }],
  [
    { name: 'DRAM', href: '/dram' },
    { name: 'DRAM Technology', href: '/dram/tech' }
  ],
  [
    { name: 'Flash', href: '/flash' },
    { name: 'Flash Technology', href: '/flash/tech' },
  ],
]

const HomePage = () => {

  return (
    <Container maxWidth="640px">
      <Text>Roadmap Tools:</Text>
      {pages.map((group, i) => (
        <Box my="0.5em" mx="-0.5em" key={i}>
          {group.map((page, p) => (
            <Button key={p} m="0.5em" as={Link} to={page.href}>{page.name} {'>'}</Button>
          ))}
        </Box>
      ))}
    </Container>
  )
}

export default HomePage
